import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { SurveyService } from 'src/app/services/survey.service';
import { UtilsService } from 'src/app/services/utils.service';

interface Survey {
  id: number;
  title: string;
  eventId: number;
  answered: boolean;
  question: Question;
  questions: any[
  // {
  //   type: string,
  //   text: string,
  //   option?: any[]
  // }
  ]
};
interface Question {
  id: number;
  title: string;
  eventId: number;
  answered: boolean;
  questions?: [
    type: string,
    text: string,
    option?: any[]
  ]
};

@Component({
  selector: 'app-admin-encuestas',
  templateUrl: './admin-encuestas.component.html',
  styleUrls: ['./admin-encuestas.component.css']
})
export class AdminEncuestasComponent implements OnInit {
  @ViewChild('form') form!: NgForm;
  public userID: number = 0;
  public eventID: number = 0;
  public loading: boolean = false;
  public disabled: boolean = false;
  
  public questions: any[] = [];
  public survey: Survey = {} as Survey;
  public question: Question = {} as Question;

  public mandatoryQuestion = [
    { id: 0, type: 'stars', text: 'En escala de 1 a 10, ¿qué tan probable es, que recomiende con otras empresas este evento?' },
    { id: 0, type: 'open', text: 'Podría indicarnos ¿qué fue lo que más le gusto del evento?' },
    { id: 0, type: 'open', text: 'Podría indicarnos ¿qué fue lo que menos le gusto o cambiaria del evento?' }
  ];

  constructor(
    private utilsService: UtilsService,
    private surveyService: SurveyService
  ) { }

  options: any[] = [];
  async ngOnInit(): Promise<void> {
    this.eventID = Number(sessionStorage.getItem('EventID'));
    this.userID = Number(sessionStorage.getItem('UserID')) | 0; // <- no deberia ser necesario
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();

    if (token && this.eventID > 0) {
      const nuevo = true; // ELIMINAR
      if (nuevo === true) {
        this.questions = this.mandatoryQuestion;
      } else {
        // CONSULTAR API
        this.questions = this.mandatoryQuestion;
      }


      // await this.loadDatabyEvent(this.eventID, this.userID);
      // if(this.survey.id > 0){
      //   // console.log('edit')
      //   this.questions = this.survey.questions;
      // } else {
      //   // console.log('new')
      //   this.survey.eventId = this.eventID;
      //   this.questions = this.mandatoryQuestion;
      // }
    }
    else if (token && this.eventID === 0) {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes seleccionado un evento');
      this.utilsService.goToPage('/admin/events/calendario-eventos');
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', '¡Debes iniciar sesión!');
      this.utilsService.goToPage('/login');
    }

    console.log(this.questions);    
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  validation_msg(name: string): boolean {
    if (this.form && this.form.controls[name]) {
      if (
        (this.form.controls[name].errors || this.form.controls[name].invalid)
        &&
        (this.form.controls[name].dirty || this.form.controls[name].touched)
      ) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  addQuestion() {
    if (this.questions.length < 8) {
      this.questions.push({ type: 'null', text: '' });
      // this.questions.push({ type: 'open', text: '' });
    }
  }

  verifyQuetion(event: any, item: any, index: number) {
    const element = document.getElementById('btn' + index);
    if (event.target.value === 'multiple') {
      this.questions[index] = {
        type: item.type,
        text: item.text,
        options: []
      }
      console.log(this.questions[index]);
    }
    else {
      element?.classList.add('showBtn');
    }
  }

  addOption(index: number) {
    if (this.questions[index].options.length < 4) {
      this.questions[index].options.push({});
      // this.questions[index].options.push('')
    }
  }


  /*
  writeOption(event: any, index_one: number, index_two: number){
    // console.log(event)
    this.disabled = false;
    console.log(event.target.value)
    this.questions[index_one].options[index_two] = event.target.value;
    // console.log(index_one)
    // console.log(index_two)
    
    // setTimeout(() => {
    //   this.questions[index_one].options[index_two] = event.target.value;
    //   this.disabled = false;
    // }, 1000)
  }
  */

  removeOption(index_one: number, index_two: number) {
    this.questions[index_one].options.splice(index_two, 1);
  }

  removeQuestion(index: number) {
    this.questions.splice(index, 1);
  }

  loadDatabyEvent(event_id: number, user_id: number) {
    this.loading = true;

    return this.surveyService.getSurveysByEvent(event_id, user_id).then(
      response => {
        // console.log(response);
        console.log(response.result);

        if (response.success) {
          this.survey.id = response.result.surveys?.id;
          this.survey.title = response.result.surveys?.title;
          this.survey.eventId = response.result.surveys?.eventId;
          this.survey.questions = response.result.surveys?.surveyQuestions;

          this.survey.questions = response.result.surveys.surveyQuestions.map((e: any) => {
            if (e.type === 'multiple') {
              const options: any[] = e.options.map((i: any) => { return { 0: i }; });
              return { id: e.id, surveyId: e.surveyId, type: e.type, text: e.text, options: options }
            } else {
              return { id: e.id, surveyId: e.surveyId, type: e.type, text: e.text }
            }
          })
        }
        else {
          console.log(response);
          this.utilsService.dialog('ERROR', 'Alerta', '¡Evento sin encuesta!');
        }
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', 'Error al cargar encuesta!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  processData() {
    // this.survey.questions = this.questions.map((e: any) => {
    //   if (e.options) {
    //     const options: any[] = e.options.map((i: any) => { return i[0]; });
    //     return { type: e.type, text: e.text, options: options }
    //   } else {
    //     return { type: e.type, text: e.text }
    //   }
    // })

    // if (this.survey.id > 0) {
    //   this.update()
    // } else {
    //   this.create();
    // }
  }

  create() {
    this.loading = true;
    this.disabled = true;

    // console.log(this.survey);
    this.surveyService.createSurveyToEvent(this.survey).then(
      (response) => {
        this.utilsService.dialog('SUCCESS', 'Éxito', 'Encuesta creada!');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al crear encuesta!');
      }).finally(() => {
        this.loading = false
        this.disabled = false;
      })
  }

  update() {
    this.loading = true;
    this.disabled = true;

    // console.log(this.survey);
    this.surveyService.updateSurveyToEvent(this.survey.id, this.survey).then(
      (response) => {
        this.utilsService.dialog('SUCCESS', 'Éxito', 'Encuesta actualizada!');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al actualizar encuesta!');
      }).finally(() => {
        this.loading = false
        this.disabled = false;
      })
  }
}




